"use client";

import React, { useState } from "react";
import ExamProgressBar from "../../../components/ExamProgressBar";
import ExamQuestion from "../../../components/ExamQuestion";
import ExamResults from "../../../components/ExamResults";
import questionsData from "../../../data/questions";
import BackButton from "../../../components/BackButton";

const ExamPage: React.FC<{ moduleId: number }> = ({ moduleId }) => {
  const allQuestions = questionsData[moduleId] || [];
  const totalQuestions = allQuestions.length;

  if (totalQuestions === 0) {
    return <p className="text-center text-red-500">No hay preguntas disponibles para este módulo.</p>;
  }

  const maxQuestions = 10;
  const randomQuestions = allQuestions.sort(() => 0.5 - Math.random()).slice(0, Math.min(maxQuestions, totalQuestions));

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState<string[]>([]);
  const [showResults, setShowResults] = useState(false);

  const handleAnswer = (answer: string) => {
    setAnswers([...answers, answer]);

    if (currentQuestionIndex + 1 < randomQuestions.length) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      setShowResults(true);
    }
  };

  const currentQuestion = randomQuestions[currentQuestionIndex];

  return (
    <div className="p-6 bg-background min-h-screen">
      {showResults ? (
        <ExamResults totalQuestions={randomQuestions.length} correctAnswers={answers.filter((a, i) => a === randomQuestions[i].correctAnswer).length} questions={randomQuestions} />
      ) : (
        <>
          <ExamProgressBar current={currentQuestionIndex + 1} total={randomQuestions.length} />
          <ExamQuestion question={currentQuestion} onAnswer={handleAnswer} questionNumber={currentQuestionIndex + 1} />
        </>
      )}
    </div>
  );
};

export default ExamPage;
