"use client";

import React from "react";

const ExamResults: React.FC<{
  totalQuestions: number;
  correctAnswers: number;
  questions: { question: string; correctAnswer: string }[];
}> = ({ totalQuestions, correctAnswers, questions }) => {
  const score = (correctAnswers / totalQuestions) * 10;

  return (
    <div className="p-6 bg-background">
      <h1 className="text-2xl font-bold">
        {score >= 9 ? "¡Felicidades!" : "¡No te rindas!"}
      </h1>
      <p className="text-neutral-600">
        {score >= 9
          ? "Aprobaste el examen. Puedes avanzar al siguiente módulo."
          : "Necesitas una calificación mínima de 9.0 para aprobar."}
      </p>
      <ul className="mt-4">
        {questions.map((q, i) => (
          <li key={i} className="flex gap-2">
            <p className="font-bold">{i + 1}.</p>
            <p>{q.question}</p>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ExamResults;
